.footer {

	&__main {
		@include section();
		background-color: $primaryColor;
	}

	&__title {
		@include h2($white);
	}

	&__contacts {
		display: flex;
		flex-direction: column;

		@media ($desktop) {
			flex-direction: row;
			justify-content: center;
		}
	}

	&__contacts-form {
		display: flex;
		flex-direction: column;
		align-items:stretch;
		margin-bottom: 4rem;

		@media ($desktop) {
			flex-basis: 50rem;
			margin-bottom: 0;
			margin-right: 4.6rem;
		}
	}

	&__input {
		@extend %input;
		margin-bottom: 2.1rem;
	}

	&__textarea {
		@extend %textarea;
		margin-bottom: 2.1rem;
		font-family: $mainFont;
	}

	&__submit {
		@extend %btn;
		align-self: center;
	}

	&__contacts-items {
		display: flex;
		flex-direction: column;
		align-items: center;

		@media ($desktop) {
			align-items: flex-start;
		}
	}

	&__links {
		display: flex;
		flex-direction: column;

		margin-bottom: 3.2rem;
	}

	&__link-item {
		display: flex;
		flex-direction: row;
		align-items: center;
		text-decoration: none;

		margin-bottom: 1.8rem;
		&:last-child {
			margin-bottom: 0;
		}

		img {
			display: block;
			margin-right: .5rem;
		}
		span {
			@include p1($white);
		}
	}


		
	&__copyright {
		background-color: $grey3;
		padding: .8rem 0 .4rem 0;

		&-text {
			text-align: center;
			font-size: 1.2rem;
			color:$secondaryColor;
			letter-spacing: .3px;
		}
	}

}