.reviews {
	@include section();
	background-color: $grey3;

	&__title {
		@include h2();
	}
	&__items {
		display: flex;
		flex-direction: column;

		.review {
			margin-bottom: 2.5rem;

			&:last-child {
				margin-bottom: 0;
			}
			@media ($desktop) {
				flex-basis: calc(50% - 1.6rem);
				margin-bottom: 0;
				
				&:nth-child(n+3) {
					margin-top: 3rem;
				}
			}
		}

		@media ($desktop) {
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-between;
		}
	}
}