.screenshot {
	display: flex;
	flex-direction: column;

	@media ($tablet) {
		flex-direction: row;
	}

	&__image {
		@include img();
		width: 100%;
		height: 15rem;
		margin-bottom: 1.5rem;

		@media ($tablet) {
			flex-shrink: 0;
			width: 20rem;
			margin-bottom: 0;
			margin-right: 1.5rem;
		}
	}

	// &__text {
		
	// }

	&__title {
		@include h3();
		padding-top: .5rem;
		margin-bottom: .4rem;
	}

	&__desc {
		@include p1();
	}
}